import axios from "axios";
import { BASE_URL } from "./app.crud";

export const LOGIN_URL = "http://154.72.192.26:6600/api/auth/login";

export let emailaddress = "";

export function login(email, passwd) {

  const loginQuery = `
    query {
      appUserLogin(email: "${email}", passwd: "${passwd}"){
        accessToken
        refreshToken
      }
    }
  `;

  emailaddress = email;
  return axios.post(LOGIN_URL, { query: loginQuery });
}

export function getAllUsers() {

const getAppUsers = `
query {
  appUsers {
    ID
    USERNAME
    FIRSTNAME
    EMAIL
    USER_ROLES {
      ID
      RVIEW
      RADD
      RUPDATE
      RDELETE
      ROLE {
        ID
        ROLENAME
        SUB_MENU_ID
      }
    }
  }
}
`;

  return axios.post(BASE_URL, { query: getAppUsers });
}

export function getUserByEmail() {
  return getAllUsers().then(response => response.data.data.appUsers.find(x => x.EMAIL === emailaddress))
}

