import * as API from "../utility/crud/app.crud"

const menuGenerator = async (user) => {

  let menuConfig = []
  let menuPaths = ['/']

  if (user) {

    menuConfig = [
      {
        id: "dashboard",
        title: "Dashboard",
        type: "item",
        icon: "Home",
        navLink: "/",
      }
    ];

    const getMainMenuQuery = `
      query {
        MainMenus {
          MENU_ID
          MENU_NAME
          ICON
          SUB_MENU {
            SUB_MENU_ID
            MAIN_MENU_ID
            SUB_MENU_NAME
            MENU_PATH
            }
        }
      }
    `;

    const mainMenus = await API.getData(getMainMenuQuery)

    const mainMenuData = mainMenus.data.data.MainMenus

    mainMenuData.forEach(data => {

      if (data.MENU_NAME !== "Dashboard") {

        const menuItem = {
          id: data.MENU_NAME.toLocaleLowerCase(),
          title: data.MENU_NAME,
          type: "collapse",
          icon: data.ICON,
          children: []
        }

        if (data.SUB_MENU.length) {
          data.SUB_MENU.forEach(item => {

            menuPaths.push(`/${item.MENU_PATH}`)

            menuItem.children.push({
              id: item.SUB_MENU_NAME.toLocaleLowerCase(),
              title: item.SUB_MENU_NAME,
              type: "item",
              icon: "Circle",
              navLink: `/${item.MENU_PATH}`,
            })

          })
        }

        menuConfig.push(menuItem)
      }

    });

  }
  // else {
  //   window.location = "/auth/logout";
  // }

  return { menuConfig, menuPaths }
}

export default menuGenerator;
